.category {
    margin-bottom: 10px;
    justify-content: space-around;
}

.catigory__head {
    margin-top: 0;
}

.catigory__item.MuiGrid-root {
    margin: 1em;
    flex: 1 1 28%;
    text-align: center;
    padding: 1em;
    border-radius: 20px;
    background-color: #fff;
}

.catigory__img {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: cover;
}

.catigory__title {
    font-size: 1.5em;
    margin-bottom: 0;
    margin-top: 0.5em;
    font-weight: bold;
}
.cart {
    padding: 0 1em;
}

.cart__title {
    margin-top: 0;
}

.cart__item {
    padding: 1.5em;
    margin-bottom: 2em;
    border-radius: 20px;
    background-color: #fff;
    display: flex;
    align-items: center;
    position: relative;
    flex-wrap: wrap;
    justify-content: center;
}

.cartview__price-one {
    font-size: 0.8em;
    color: grey;
    margin-top: -5px;
    margin-left: 0.5em;
}

.cartview__count {
    display: inline-block;
    border: 1px solid;
    background-color: #fff;
    padding: 6px 16px;
    border-radius: 9px;
    margin: 5px;
    vertical-align: middle;
}

.cart__item .MuiButtonBase-root.MuiButton-containedPrimary {
    min-width: auto;
}



@media screen and (min-width: 900px) {
    .cart__item {
        flex-wrap: nowrap;
    }
    .cartview__count {
        padding: 6px 10px;
    }
    .cartview__price {
        font-size: 20px;
    }
}
.cartview__item.MuiGrid-root {
    padding: 1em;
    margin-bottom: 1.5em;
    border-radius: 20px;
    transition: 0.3s;
}

.cartview__item--preview.MuiGrid-root {
    padding: 0.5em;
    margin-bottom: 0.5em;
    border-radius: 20px;
    transition: 0.3s;
    position: relative;
}

.cartview__item:hover {
    box-shadow: inset 0 0 10px 1px lightgrey;
}

.cartview__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #FFFFFF;
    border-radius: 20px;
    padding: 1em;
    min-height: 144px;
}

.cartview__title,
.cartview__price {
    font-weight: 500;
    font-size: 15px;
    vertical-align: baseline;
    margin: 0.5em;
}

.cartview__title {
    font-size: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.cartview__price svg,
.cartview__price-one svg {
    vertical-align: sub;
}

.cartview__cat {
    display: block;
    text-decoration: none;
    color: #12805D;
    font-weight: bold;
    transition: 0.3s;
    margin: 0.5em;
}

.cartview__cat:hover {
    color: black
}

.cartview__img-item {
    flex: 1 1 50%;
    padding: 0.5em;
}

.cartview__footer {
    margin-top: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.pagination {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
}

.pagination__item {
    padding: 8px 15px;
    border-radius: 5px;
    background-color: transparent;
    margin-right: 3px;
    transition: 0.3s;
    cursor: pointer;
}

.pagination__item:hover,
.pagination__item.active {
    background-color: black;
    color: #fff;
}
.login .MuiFormControl-root {
    margin-bottom: 1.5em;
    display: block;
}

.login .css-1ptx2yq-MuiInputBase-root-MuiInput-root:after {
    border-color: #12805D;
}

.login .css-1c2i806-MuiFormLabel-root-MuiInputLabel-root.Mui-focused {
    color: #12805D;
    font-weight: 600;
}

.login .MuiButtonBase-root.MuiButton-root {
    margin-right: 1em;
}
.MuiMenuItem-root.menu__item {
    font-size: inherit;
    padding: 0;
    margin-bottom: 15px;
    position: relative;
}

.menu__item-num {
    position: absolute;
    bottom: -4px;
    left: 16px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: block;
    text-align: center;
    font-size: 10px;
    font-weight: bold;
    line-height: 18px;
    color: #fff;
    background-color: crimson;
}

.menu__link {
    text-decoration: none;
    color: #1A1F16;
    padding: 5px;
    transition: 0.1s;
    border-radius: 10px;
    width: 100%;
}

.menu__link:hover,
.menu__link:active,
.menu__link.active {
    background: #1A1F16;
    color: #fff
}

.menu__link svg {
    vertical-align: bottom;
}
.main-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 15px;
    grid-gap: 20px;
    gap: 20px;
    background: #FFFFFF;
    border-radius: 8px;
    height: calc(100vh - 64px);
    font-size: 0;
    width: 50px;
    transition: 0.5s;
}

.main-nav__logout.MuiButtonBase-root {
    margin-top: auto;
    background-color: crimson;
    color: #FFFFFF;
    border-radius: 9px;
}

.main-nav__logout.MuiButtonBase-root:hover {
    background-color: rgba(220, 20, 60, 0.475);
}
.cartpreview {
    padding: 1em;
}

.cartview__item:hover .cartpreview__del.MuiButtonBase-root {
    opacity: 1;
}

.cartpreview__del.MuiButtonBase-root {
    position: absolute;
    top: 0;
    right: 0;
    transition: 0.3s;
}

.cartpreview .cartpreview__del.MuiButtonBase-root {
    opacity: 0;
}

.cartpreview .cartview__link {
    min-height: auto;
}

.cartpreview__title {
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 1em;
    margin-top: 0;
}

@media screen and (min-width: 900px) {
    .cartpreview__title {
        font-size: 2em;
    }
}

.cartpreview__total {
    font-size: 20px;
    text-align: center;
}

.cartpreview__button {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    background-color: #1A1F16;
    padding: 8px;
    border-radius: 9px;
    width: 100%;
    text-align: center;
    margin-top: 1em;
}

.cartpreview__button svg,
.cartpreview__total svg {
    vertical-align: bottom;
}

.cartpreview__button:hover {
    background-color: #12805D;
}
.gridbox {
    max-height: 100%;
    overflow-y: hidden;
    padding: 1em;
}

.gridbox__page {
    padding: 1em 0;
    max-height: calc(100vh - 2em);
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: rgba(26, 31, 22, 0.5); 
}

.gridbox__page::-webkit-scrollbar {
    width: 6px;
    border-radius: 20px;
    background-color: rgba(26, 31, 22, 0.1);
}

.gridbox__page::-webkit-scrollbar-thumb {
    background-color: rgba(26, 31, 22, 0.5);
    border-radius: 20px;
}
#root, .App {
  max-height: 100vh;
  overflow: hidden;
}

body {
  margin: 0;
  font-family: 'Cabin', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EDEDED;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

